import React, { useState } from "react";
import "../styles/Footer.scss";
import Footer from 'rc-footer';
import 'rc-footer/assets/index.css';
import rsburger from '../assets/rsburger.png'
import { Button, Modal } from 'react-bootstrap'


const FooterRS = () => {
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    return (
        <>
            <Footer className='Footer'
                columns={[
                    {
                        title: 'Horaires',
                        items: [
                            {
                                title: 'Du Lundi au Vendredi et Dimanche :',

                            },
                            {
                                title: '12:0015:00, 19:0023:00',
                            },
                            {
                                title: 'Samedi :',

                            },
                            {
                                title: '19:0001:00',
                            },
                        ],
                    },
                    {
                        icon: (
                            <img src={rsburger} alt="Rsburger" />
                        ),
                        title: 'RS Burger',
                        items: [
                            {
                                title: <span> 2018 / {new Date().getFullYear()} - RS Burger - <span className="mention" onClick={handleShow}>Mention lgales</span></span>

                            },
                        ],
                    },
                ]}
                bottom="Dvelopp et design par Zerga Illyes." 
            />

            <Modal
                show={show}
                onHide={handleClose}
                keyboard={false}
            >
                <Modal.Header closeButton>
                    <Modal.Title>Mentions lgales</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    En vertu de larticle 6 de la loi n 2004-575 du 21 juin 2004 modifi par la loi n2011-525 du 17 mai 2011 pour la confiance dans lconomie numrique,
                    il est prcis aux utilisateurs du site rsburger.fr lidentit des diffrents intervenants dans le cadre de sa ralisation et de son suivi.
                </Modal.Body>
                <Modal.Body>
                    SASU Socit par actions simplifie  associ unique RS Burger, au capital de 100 
                </Modal.Body>
                <Modal.Body>
                    Entreprise sous le numro : 84031352200012.
                </Modal.Body>
                <Modal.Body>
                    Responsable de publication : Illyes Zerga
                </Modal.Body>
                <Modal.Body>
                    Hbergement : 
                </Modal.Body>
                <Modal.Body>
                Signalement de contenus inappropris ou illgaux : 
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Fermer
                    </Button>
                </Modal.Footer>
            </Modal>
        </>
    )

}

export default FooterRS;
